.wp-block-blocks-gamestore-games-line{

.games-line-container{
width: 100%;
overflow: hidden;
position: relative;

> .swiper-wrapper{
display: flex;
transition: transform 3500ms linear;
transition-timing-function: linear;

}

.game-item{
	width: 267px;
	height: 403px;
	position: relative;
	border-radius: 8px;
	margin-left: 4px;
	margin-right: 4px;
	
	img{
		object-fit: cover;
		width: 100%;
	}
	} 
}	  

}  